'use strict';
import refs from '../index.js';
import showPromo from './showPromo.js';
// поиск фильма
// console.log('page :', page);
const filmSearch = (event, page = 1) =>{
    // event.preventdefault;
    console.log('page :', page);
    refs.film_promo.forEach(promo => {
        refs.films_block.removeChild(promo);
    })
    // if(refs.film_search_form.firstElementChild.value === ''){
    //     break;
    // }
    let searchApi = `https://api.themoviedb.org/3/search/movie?api_key=4aa539255aa0c2506cf45806a15a8a0a&language=en-US&page=${page}&include_adult=false&query=${refs.film_search_form.firstElementChild.value}`
    showPromo(searchApi);
    return searchApi, page;

}
export default filmSearch;

